"use client";

import React from 'react'; // Ensure React is imported for cloneElement
import { 
  Smartphone, 
  Shirt, 
  Home, 
  Trophy, 
  Sparkles, 
  ChevronRight 
} from "lucide-react";

const categories = [
  { id: 1, name: "Electronics", icon: <Smartphone />, color: "bg-blue-50 text-blue-600" },
  { id: 2, name: "Fashion", icon: <Shirt />, color: "bg-pink-50 text-pink-600" },
  { id: 3, name: "Home & Kitchen", icon: <Home />, color: "bg-orange-50 text-orange-600" },
  { id: 4, name: "Sports", icon: <Trophy />, color: "bg-green-50 text-green-600" },
  { id: 5, name: "Beauty", icon: <Sparkles />, color: "bg-purple-50 text-purple-600" },
];

export default function CategoryList() {
  return (
    <section className="py-12">
      <div className="flex items-center justify-between mb-8 px-4">
        <div>
          <h2 className="text-3xl font-bold tracking-tight text-gray-900">
            Shop by Category
          </h2>
          <p className="text-gray-500 mt-1 text-sm">Find exactly what you&apos;re looking for.</p>
        </div>
        <button className="flex items-center gap-1 text-sm font-semibold text-indigo-600 hover:text-indigo-700 transition-colors">
          View All <ChevronRight className="h-4 w-4" />
        </button>
      </div>

      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-6 px-4">
        {categories.map((cat) => (
          <div
            key={cat.id}
            className="group relative flex flex-col items-center justify-center p-8 rounded-[2rem] bg-white border border-gray-100 shadow-sm transition-all duration-300 hover:shadow-xl hover:-translate-y-2 cursor-pointer"
          >
            {/* Icon Container - Fixed line below */}
            <div className={`mb-4 flex h-16 w-16 items-center justify-center rounded-2xl transition-transform duration-300 group-hover:scale-110 group-hover:rotate-3 ${cat.color}`}>
              {React.cloneElement(cat.icon, { size: 32, strokeWidth: 1.5 })}
            </div>

            <span className="text-sm font-bold text-gray-900 group-hover:text-indigo-600 transition-colors">
              {cat.name}
            </span>

            <div className="absolute top-4 right-4 opacity-0 transition-opacity group-hover:opacity-100">
              <div className="h-6 w-6 rounded-full bg-gray-900 flex items-center justify-center">
                <ChevronRight className="h-3 w-3 text-white" />
              </div>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
}
