"use client";

import React from 'react';
import { ShoppingCart, Star, Heart } from "lucide-react";

const products = [
  { id: 1, name: "iPhone 15 Pro", price: 150000, image: "/p1.png", category: "Mobile" },
  { id: 2, name: "Dell XPS 13", price: 180000, image: "/p2.png", category: "Laptops" },
  { id: 3, name: "Sony WH-1000XM5", price: 12000, image: "/p3.png", category: "Audio" },
  { id: 4, name: "Apple Watch Series 9", price: 30000, image: "/p4.png", category: "Wearables" },
];

export default function FeaturedProducts() {
  // Format price to Sri Lankan Rupees style
  const formatPrice = (amount) => {
    return new Intl.NumberFormat('en-LK', {
      style: 'currency',
      currency: 'LKR',
      maximumFractionDigits: 0,
    }).format(amount).replace("LKR", "Rs.");
  };

  return (
    <section className="py-16 px-4 max-w-7xl mx-auto">
      <div className="flex items-end justify-between mb-10">
        <div>
          <span className="text-indigo-600 font-bold tracking-widest text-xs uppercase">Weekly Picks</span>
          <h2 className="text-3xl md:text-4xl font-black text-gray-900 mt-1">Featured Products</h2>
        </div>
        <button className="text-sm font-bold border-b-2 border-black pb-1 hover:text-indigo-600 hover:border-indigo-600 transition-all">
          View All
        </button>
      </div>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-8">
        {products.map((product) => (
          <div
            key={product.id}
            className="group relative flex flex-col"
          >
            {/* Image Container */}
            <div className="relative aspect-square overflow-hidden rounded-[2rem] bg-gray-50 border border-gray-100 transition-all duration-500 group-hover:shadow-2xl group-hover:shadow-indigo-100">
              
              {/* Wishlist Button */}
              <button className="absolute top-4 right-4 z-10 p-2 rounded-full bg-white/80 backdrop-blur-sm text-gray-400 hover:text-red-500 transition-colors shadow-sm">
                <Heart size={18} />
              </button>

              <img
                src={product.image}
                alt={product.name}
                className="w-full h-full object-contain p-8 transition-transform duration-500 group-hover:scale-110"
              />
              
              {/* Quick Add Overlay (2026 Trend) */}
              <div className="absolute inset-x-0 bottom-0 p-4 translate-y-full transition-transform duration-300 group-hover:translate-y-0 bg-gradient-to-t from-white/90 to-transparent backdrop-blur-[2px]">
                <button className="w-full bg-black text-white py-3 rounded-2xl font-bold flex items-center justify-center gap-2 hover:bg-indigo-600 transition-colors shadow-lg">
                  <ShoppingCart size={18} />
                  Quick Add
                </button>
              </div>
            </div>

            {/* Content */}
            <div className="mt-5 space-y-1">
              <div className="flex items-center justify-between">
                <span className="text-xs font-semibold text-gray-400 uppercase tracking-tighter">{product.category}</span>
                <div className="flex items-center gap-1">
                  <Star size={12} className="fill-yellow-400 text-yellow-400" />
                  <span className="text-xs font-bold text-gray-600">4.8</span>
                </div>
              </div>
              
              <h3 className="text-lg font-bold text-gray-900 group-hover:text-indigo-600 transition-colors truncate">
                {product.name}
              </h3>

              <div className="flex items-center gap-2">
                <p className="text-xl font-black text-gray-900">
                  {formatPrice(product.price)}
                </p>
                {/* Visual anchor for discounts (Optional) */}
                <span className="text-xs font-medium text-green-600 bg-green-50 px-2 py-0.5 rounded-md">Free Delivery</span>
              </div>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
}
